<template>
  <!-- 环形进度条 -->
  <a-progress type="circle" :percent="percent" :strokeWidth="10" :width="170" :strokeColor="strokeColor"/>
</template>
<script>
export default {
  name: "circleBar",
  data() {
    return {
      strokeColor: "#333333"
    };
  },
  props: {
    percent: Number
  },
  created() {
    console.log("presentNum", this.percent);
  }
};
</script>




