<template>
  <div class="showContent detailContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <backSubMenu :backTitle="backTitle" :lineUnder="false"></backSubMenu>
    <div class="detail">
      <div class="left">
        <div class="detailCricle">
            <p>Similar</p>
            <div>
              <circleBar :percent="score"></circleBar>
              <div class="des" v-if="scoreFlag=='A' ||scoreFlag=='B'"><p>Congratulations</p><p>You get a hign score</p></div>
              <div class="des" v-if="scoreFlag=='C' ||scoreFlag=='D'"><p>Bad</p><p>You get a low score</p></div>
            </div>
        </div>
      </div>
      <div class="right">
        <div class="infor_form_detail">
          <div>
            <!-- Time -->
            <label>Time</label>
            <input type="text" :value="$util.timestampToDay(record.created_at)" readonly>
          </div>
          <!-- <div>
            <label>Event</label>
            <input type="text" :value="detailData.name" readonly>
          </div> -->
          <div class="hasDot">
            <!-- Situation -->
            <label>Situation</label>
            <input type="text" :value="record.status" readonly>
            <span :class="record.status=='success'?'greenDot':'redDot'" id="statusDot">
              <i class="iconfont amd-dot"></i>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import { mapGetters } from "vuex";
import backSubMenu from "@/components/backSubMenu";
import circleBar from "@/components/circleBar";
export default {
  data() {
    return {
      routekey: ["idHoldingRequest", "idHoldingHistory;idHoldingHistoryDetail"],
      loading: false,
      score: "0.00",
      backTitle: "Details",
      resultData: "",
      record:"",
    };
  },
  components: {
    requestAndHisSubMenu,
    circleBar,
    backSubMenu
  },
  computed: {
    ...mapGetters(["apiHistoryList"]),
    scoreFlag(){
      if(this.score>=0 && this.score<40){   //0-40
          return 'D';
      }else if(this.score>=40 && this.score<60){//40-60
          return 'C';
      }else if(this.score>=60 && this.score<80){//60-80
          return 'B';
      }else{
          return 'A';
      }
    }
  },
  methods: {
    getDetailData() {
      let params = this.$route.params;
      console.log(params);
      this.record=params.record;
      this.resultData = params.record.response.data;
      console.log(this.resultData);
      let percent = this.resultData.score;
      if(percent){
        this.score=Number(percent.replace('%',""));
        console.log(this.score);
      }
    }
  },
  created() {
    if(this.$route.params.record){
      this.getDetailData();
    }else{
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less">
@import "../../../css/page.less";
</style>



